import React from 'react'

export const PrincipalPage = () => {
  return (
    <div class='text-center'>
    <h1>Herramientas</h1>
    <p>Del latín ferramenta, una herramienta es un instrumento que permite desarrollar ciertos trabajos. Estos objetos fueron diseñados para facilitar la realización de una tarea mecánica que requiere del uso de una cierta fuerza. El destornillador, la pinza y el martillo son herramientas.</p>
    <img src="https://definicion.de/wp-content/uploads/2010/06/herramienta-1.jpg" alt="" width="400px" />
    <h2>Su uso en el trabajo</h2>
    <p>Herramientas también son los utensilios específicos que utilizan los artesanos u otros trabajadores para el desarrollo de sus tareas especializadas: “Mi tía hace artesanías y tiene un montón de herramientas para crear sus obras”, “La maestra me pidió una herramienta para trabajar con madera”.
          Actualmente existen herramientas muy específicas para distintos ámbitos. Así, por ejemplo, en el caso del carpintero habría que destacar la sierra, la lija o el serrucho. Mientras que en el caso del jardinero, este utilizará tijeras, cortadora de césped, pala, pico.</p>
     <img src="https://definicion.de/wp-content/uploads/2010/06/trabajadores.jpg" alt="" width="400px" />
     <h2>Tipos de herramientas</h2>
     <p>Las herramientas, a groso modo, se pueden clasificar en tres tipos: las manuales, las mecánicas y las tecnológicas.</p>
     <p>1. Las herramientas manuales son aquellas que utilizan la fuerza física del cuerpo humano como punto de partida, aumentándola o enfocándola de modo tal que tareas que exigen mucho esfuerzo se realicen más fácilmente. Son un perfecto ejemplo de ello los martillos (el brazo humano debe dar el golpe), los destornilladores (la mano humana debe inducir al giro) o los cuchillos (el brazo humano debe conducir el corte).</p>
     <img src="data:image/jpeg;base64," alt="" width="300px" />
     <p>2. Las herramientas mecánicas, en cambio, son aquellas que utilizan una fuente de energía externa como punto de partida, y convierten dicha energía en trabajo, ya sea en combinación con cierto grado de esfuerzo físico humano, o sin él. Por ejemplo, un martillo hidráulico emplea electricidad para triturar el cemento, pero aún así requiere de una persona que lo sostenga y lo dirija; y un soplete quema y funde los materiales gracias a la combustión de ciertos químicos inflamables, pero la fuerza humana debe apuntarlo y sostenerlo.</p>
     <img src="data:image/jpeg;base64," alt="" width="300px" />
     <p>3. Las herramientas tecnológicas, por último, poseen un mayor grado de complejidad que les permite un mayor nivel de automatización, liberando parcialmente al cuerpo humano de su participación en las labores. Por ejemplo, las aplicaciones que instalamos en nuestro teléfono y nos permiten llevar a cabo cómodamente tareas como fotografiar, escanear documentos, cronometrar el tiempo, etc.</p>
     <img src="data:image/jpeg;base64," alt="" width="300px" />
     <p>Además de esta clasificación, también pueden clasificarse las herramientas de acuerdo a la función específica que desempeñan, de la siguiente manera:</p>
     <p>ºHerramientas de montaje, que sirven para ensamblar (o desensamblar) muebles, partes o aparatos, como son por ejemplo los destornilladores o las llaves.</p>
     <img src="data:image/jpeg;base64," alt="" width="200px" />
     <p>ºHerramientas de sujeción, que sirven para sujetar piezas o materiales y así permitir su manipulación más segura (para las manos) o imprimirles mayor fuerza. Son ejemplo de ello las pinzas y los alicates.</p>
     <img src="data:image/jpeg;base64," alt="" width="200px" />
     <p>ºHerramientas de golpe, que sirven como su nombre lo indica para imprimir fuerza sobre una superficie, maximizando el impacto que podría tener la mano humana. Son ejemplo de ello el martillo y el pico.</p>
     <img src="data:image/jpeg;base64," alt="" width="200px" />
     <p>ºHerramientas de unión, que sirven para juntar piezas o materiales difíciles de acoplar, como lo hacen por ejemplo los sopletes o las pistolas de silicona.</p>
     <img src="data:image/jpeg;base64," alt="" width="200px" />
     <p>ºHerramientas de medición o trazado, que sirven para, justamente, medir y cotejar superficies o comparar magnitudes con otras. Es el caso de los compases, las cintas métricas y las escuadras.</p>
     <img src="data:image/jpeg;base64," alt="" width="200px" />
     <p>ºHerramientas de corte, que sirven para separar o cortar materiales, como es el caso de las tijeras, los cuchillos o las seguetas.</p>
     <img src="data:image/jpeg;base64," alt="" width="200px" />
    </div>
  )
}
